//  Last updated: Version 3.7.1 on 2018-11-02
$base-font-size: 16px;
$em-base: $base-font-size; // used for bourbon
$base-mobile-font-size: 15px;
$extra-tiny-font-px: 12px;
$tiny-font-px: 13px;
$small-font-px: 18px;
$medium-font-px: 20px;
$large-font-px: 28px;
$xl-font-px: 36px;
$xxl-font-px: 60px;
$max-typography-styles: 8;
$color-array:( inky-blue: #0D1C3D, dolphin: #677083, white: #fff);
$weight-array:( book: "Gotham 4r", light: "Gotham 3r", medium: "Gotham 5r", bold: "Gotham 7r");
$backup-font-family: Verdana;
$line-height-difference--large: 14px;
$line-height-difference--medium: 10px;

$locked: false;
// Functions
@each $color-name,
  $color-value in $color-array {
  // single-dash is preferred, but will support double-dash for legacy implementations
  .-#{$color-name},
  .\--#{$color-name} { // escapes double hyphen for IE and legacy mobile Safari suport
    color: $color-value;
  }

}

@mixin caps {
  text-transform: uppercase;
}

@each $weight-name,
  $weight-value in $weight-array {
  // single-dash is preferred, but will support double-dash for legacy implementations
  .-#{$weight-name},
  .\--#{$weight-name},
  %#{$weight-name} { // escpaed double hyphen for IE and legacy mobile Safari suport
    font-family: $weight-value;
  }
}

@function px-to-rem($size) {
  $remSize: $size / 16px;
  @return #{$remSize}rem;
}

@function px-to-em($pixels, $context: $base-font-size) {
  @return #{$pixels/$context}em;
}

//Mixin for size
@mixin calculateRem($size) {
  font-size-pixel: $size; // fallback in px
  font-size-rem: calculateRem($size);
}

//Mixin for JKP projects
@function large-line-height($line-height) {
  @return $line-height-difference--large - $font-size;
}

@function medium-line-height($line-height) {
  @return $line-height-difference--medium + $font-size;
}

@function small-line-height($line-height) {
  @return $line-height / $font-size;
}

$extra-tiny-font-size: px-to-rem($extra-tiny-font-px);
$tiny-font-size: px-to-rem($tiny-font-px);
$small-font-size: px-to-rem($small-font-px);
$medium-font-size: px-to-rem($medium-font-px);
$large-font-size: px-to-rem($large-font-px);
$xl-font-size: px-to-rem($xl-font-px);
$xxl-font-size: px-to-rem($xxl-font-px);
$base-font-family: "Gotham 4r";
$light-font-family: "Gotham 3r";
$medium-font-family: "Gotham 5r";
$bold-font-family: "Gotham 5r"; // no such thing as bold, revert to medium
%inverted-link {
  &,
  &:visited {
    color: $text-light;
  }
  &:hover,
  &:focus {
    color: color(interactive-blue);
  }
}

@mixin styling($i) {
  @if $i==1 {
    font-size: $xxl-font-size;
    line-height: 64px;
    letter-spacing: -1px;
    font-weight: normal;
    @include media($mobile) {
      body:not(.-locked) &:not(.-locked) {
        font-size: $xl-font-size;
        line-height: 40px;
      }
    }
    @at-root #{&}-marketing {
      font-size: $xxl-font-size;
      line-height: 74px;
      @include media($mobile) {
        body:not(.-locked) &:not(.-locked) {
          font-size: $xl-font-size;
          line-height: 50px;
        }
      }
    }
  }
  @if $i==2 {
    font-size: $xl-font-size;
    line-height: 40px;
    font-weight: normal;
    @include media($mobile) {
      body:not(.-locked) &:not(.-locked) {
        font-size: $large-font-size;
        line-height: 32px;
      }
    }
    @at-root #{&}-marketing {
      font-size: $xl-font-size;
      line-height: 50px;
      @include media($mobile) {
        body:not(.-locked) &:not(.-locked) {
          font-size: $large-font-size;
          line-height: 42px;
        }
      }
    }
  }
  @if $i==3 {
    font-size: $large-font-size;
    font-weight: normal;
    line-height: 32px;
    @include media($mobile) {
      body:not(.-locked) &:not(.-locked) {
        font-size: $medium-font-size;
        line-height: 24px;
      }
    }
    @at-root #{&}-marketing {
      font-size: $large-font-size;
      font-weight: normal;
      line-height: 42px;
      @include media($mobile) {
        body:not(.-locked) &:not(.-locked) {
          line-height: 30px;
        }
      }
    }
  }
  @if $i==4 {
    font-size: $medium-font-size;
    line-height: 24px;
    font-weight: normal;
    @include media($mobile) {
      body:not(.-locked) &:not(.-locked) {
        font-size: $small-font-size;
        line-height: 22px;
      }
    }
    @at-root #{&}-marketing {
      font-size: $medium-font-size;
      line-height: 30px;
      @include media($mobile) {
        body:not(.-locked) &:not(.-locked) {
          font-size: $small-font-size;
          line-height: 28px;
        }
      }
    }
  }
  @if $i==5 {
    font-size: $small-font-size;
    line-height: 22px;
    font-weight: normal;
    &-marketing {
      font-size: $small-font-size;
      line-height: 28px;
    }
  }
  @if $i==6 {
    font-size: $base-font-size;
    line-height: 22px;
    font-weight: normal;
    @include media($mobile) {
      body:not(.-locked) &:not(.-locked) {
        font-size: $base-mobile-font-size;
        line-height: 20px;
      }
    }
    @at-root #{&}-marketing {
      font-size: $base-font-size;
      line-height: 26px;
      @include media($mobile) {
        body:not(.-locked) &:not(.-locked) {
          font-size: $base-mobile-font-size;
          line-height: 24px;
        }
      }
    }
  }
  @if $i==7 {
    font-size: $base-font-size;
    line-height: 22px;
    font-weight: normal;
    @include media($mobile) {
      font-size: $base-mobile-font-size;
      line-height: 20px;
    }
    @at-root #{&}-marketing {
      font-size: $base-font-size;
      line-height: 24px;
      @include media($mobile) {
        font-size: $base-mobile-font-size;
        line-height: 24px;
      }
    }
  }
  @if $i==8 {
    font-size: $tiny-font-size;
    line-height: 18px;
    font-weight: normal;
    @at-root #{&}-marketing {
      font-size: $tiny-font-size;
      line-height: 22px;
    }
    // single-dash is preferred, but will support double-dash for legacy implementations
    &.-caps,
    &.\--caps { // escapes double hyphen for IE and legacy mobile Safari suport
      @include caps;
    }
  }
  @if $i==9 {
    font-size: $extra-tiny-font-size;
    line-height: 16px;
    font-weight: normal;
    @at-root #{&}-marketing {
      font-size: $extra-tiny-font-size;
      line-height: 18px;
    }
    // single-dash is preferred, but will support double-dash for legacy implementations
    &.-caps,
    &.\--caps { // escapes double hyphen for IE and legacy mobile Safari suport
      @include caps;
    }
  }
}

//Marketing Mixin
@mixin styling-marketing($i) {
  @include styling($i);
  @if $i==1 {
    line-height: 74px;
    @include media($mobile) {
      line-height: 50px;
    }
  }
  @if $i==2 {
    line-height: 50px;
    @include media($mobile) {
      line-height: 42px;
    }
  }
  @if $i==3 {
    line-height: 42px;
    @include media($mobile) {
      line-height: 30px;
    }
  }
  @if $i==4 {
    line-height: 30px;
    @include media($mobile) {
      line-height: 28px;
    }
  }
  @if $i==5 {
    line-height: 28px;
  }
  @if $i==6 {
    line-height: 26px;
    @include media($mobile) {
      line-height: 24px;
    }
  }
  @if $i==7 {
    line-height: 24px;
    @include media($mobile) {
      line-height: 24px;
    }
  }
  @if $i==8 {
    line-height: 22px;
  }
  @if $i==9 {
    line-height: 18px;
  }
}

//Create class names based on this
// outputs as styling-1, styling-2, etc..
// marketing output as styling-1-marketing
@for $i from 1 through 9 {
  .styling-#{$i} {
    @include styling($i)
  }
}

// For future aem/authoring who want jkp line height at a global level
.kp-body-component[data-body-content="marketing"] {
  @for $i from 1 through 9 {
    .styling-#{$i} {
      @include styling-marketing($i);
    }
  }
}

.inverted-link {
  @extend %inverted-link;
}

.muted {
  color: darken(color(graphite-dark), 20%);
}

// deprcated but still supported styles
.hero-heading {
  @include styling-marketing(1);
}

.page-heading {
  @include styling-marketing(2);
}

.section-heading {
  @include styling-marketing(3);
}

.item-heading {
  @include styling-marketing(4);
}

.item-tag {
  @include styling-marketing(5);
}

.auxiliary-text {
  @include styling-marketing(6);
}
