// Last updated on 2019-06-13 by Eric Yeh for SG v3.9.17

@import 'checkboxes-lib';

// ADA compliant checkbox
// Note: "@mixin check-box" AND "@mixin check-box-class" contains the same code logic as of 2019-06-01. "@mixin check-box-class" is created temporarily in order to reconcile the issues encountered when merging "@import checkboxes-lib" in checkboxes.scss
@mixin check-box-class(
	$check-box-font: $check-box-font-variation-1,
	$check-box-size: $check-box-size-variation-1,
	$check-box-checkmark-size: $check-box-checkmark-size-variation-1,
	$check-box-checkmark-position-top:
		$check-box-checkmark-position-top-variation-1,
	$check-box-checkmark-position-left:
		$check-box-checkmark-position-left-variation-1,
	$check-box-space-right: $check-box-space-right-variation-1,
	$check-box-space-bottom: $check-box-space-bottom-variation-1
) {
	// hide the real checkbox
	position: absolute;
	margin-top: $check-box-space-bottom/2;
	opacity: 0;

	+ label {
		display: inline-block;
		position: relative;
		margin: $check-box-space-bottom/2 0;
		padding: $check-box-border-width 0 0 $check-box-size +
			$check-box-space-right - $check-box-border-width;
		min-height: $check-box-size;
		font-size: $check-box-font;
		line-height: 1em;
		color: $text-dark;

		// empty-box of the checkbox facade
		span[aria-hidden='true']:before {
			content: '';
			position: absolute;
			top: 0;
			left: 0;
			width: $check-box-size;
			height: $check-box-size;
			border: $check-box-border-width solid $check-box-default-color;
		}
	}

	// updated on 2018-11-02 by Nancy
	// Needed to have braced scoped to checkbox class
	@at-root #{&}:focus + label span[aria-hidden='true']:before {
		@include checkbox-focus-outline;
	}

	&[type='checkbox']:checked + label {
		span[aria-hidden='true']:before {
			background-color: $check-box-default-color;
		}

		// check mark of the checkbox facade
		span[aria-hidden='true']:after {
			@include icon-for('checkmark');
			position: absolute;
			top: $check-box-checkmark-position-top;
			left: $check-box-checkmark-position-left;
			font-size: $check-box-checkmark-size;
			color: #fff;
		}
	}

	&[type='checkbox']:disabled + label {
		color: $check-box-disable-text;
		cursor: not-allowed;

		span[aria-hidden='true']:before {
			border-color: $check-box-disable-color;
		}
	}

	&[type='checkbox']:disabled:checked + label span[aria-hidden='true']:before {
		background-color: $check-box-disable-color;
	}
}

.check-box {
	@include check-box-class();

	&.-variation-1 {
		@include check-box-class(
			$check-box-font-variation-1,
			$check-box-size-variation-1,
			$check-box-checkmark-size-variation-1,
			$check-box-checkmark-position-top-variation-1,
			$check-box-checkmark-position-left-variation-1,
			$check-box-space-right-variation-1,
			$check-box-space-bottom-variation-1
		);
	}

	&.-variation-2 {
		@include check-box-class(
			$check-box-font-variation-2,
			$check-box-size-variation-2,
			$check-box-checkmark-size-variation-2,
			$check-box-checkmark-position-top-variation-2,
			$check-box-checkmark-position-left-variation-2,
			$check-box-space-right-variation-2,
			$check-box-space-bottom-variation-2
		);
	}

	&.-variation-3 {
		@include check-box-class(
			$check-box-font-variation-3,
			$check-box-size-variation-3,
			$check-box-checkmark-size-variation-3,
			$check-box-checkmark-position-top-variation-3,
			$check-box-checkmark-position-left-variation-3,
			$check-box-space-right-variation-3,
			$check-box-space-bottom-variation-3
		);
	}

	// Typography styling($i) override - force consistent checkmark positioning even when typography styling is applied to label
	@include media($device) {
		body:not(.-locked) .check-box + label:not(.-locked) {
			line-height: 1em;
		}
		@include media($mobile) {
			body:not(.-locked) .styling-1:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-2:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-3:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-4:not(.-locked) {
				font-size: 16px;
			}
		}
		@include media($tablet-up) {
			body:not(.-locked) .styling-1:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-2:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-3:not(.-locked) {
				font-size: 16px;
			}
			body:not(.-locked) .styling-4:not(.-locked) {
				font-size: 16px;
			}
		}
	}
}

.checkbox-toggle-switch {
	@include checkbox-toggle-switch;
}

// Default INPUT type="checkbox" styles (should not be used).
// These styles are created for the sole purpose of neutralize the issues caused by the incorrect use of checkbox mixin within kp-foundation.
// Specifically, the use of the line @include checkbox($checkbox-size-mobile); in _global-styles.scss without a specified css class selector.
// To utilize the StyleGuide styled checkbox, please use className="check-box" for all INPUT type="checkbox" PLUS the addition of <span aria-hidden="true"> inside its matching LABEL in your project.
input[type='checkbox']:not(.check-box):not(.checkbox-toggle-switch) {
	position: absolute;
	display: inline-block;
	margin: 12px 0 0 2px;
	width: 20px;
	height: 20px;
	border: $check-box-border-width solid $check-box-default-color;
	//Un-do browser specific CSS extensions set in _forms.scss
	-moz-appearance: none;
	-webkit-appearance: none;

	& + label {
		display: inline-block;
		margin: 10px 0 0;
		padding-top: 2px;
		padding-left: 40px;
		font-size: $base-font-size;
	}
}

input[type='checkbox']:checked:not(.check-box):not(.checkbox-toggle-switch) {
	//Un-do browser specific CSS extensions set in _forms.scss
	-moz-appearance: checkbox;
	-webkit-appearance: checkbox;
}

input[type='checkbox']:focus:not(.check-box):not(.checkbox-toggle-switch) {
	@include checkbox-focus-outline;
}

input[type='checkbox']:disabled:not(.check-box):not(.checkbox-toggle-switch) {
	border-color: $check-box-disable-color;
}

// Remove this mixin once all teams have updated their code to use "check-box".
// This is a Non-ADA compliant checkbox, set for deprecation in future SG version.
// Keeping this mixin alive to prevent build error in kp-foundation _global-styles.scss where @include checkbox($checkbox-size-mobile) is used.
// The :not(.check-box) selectors are added to prevent conflicts with "check-box"
$checkbox-border-width: 2px;
$checkbox-size-mobile: 20px;
$enabled-color: color('dolphin');
$disabled-color: color('graphite');
@mixin checkbox($size, $label-size: $base-font-size) {
	&[type='checkbox']:not(:checked):not(.check-box),
	&[type='checkbox']:checked:not(.check-box) {
		position: absolute;
		opacity: 0;

		& + label {
			margin: $size/2 0 0 0;
			color: $text-dark;
			cursor: pointer;
			display: inline-block;
			font-size: $label-size;
			position: relative;
			padding-left: $size + $small-spacing;
			padding-top: $checkbox-border-width;
			min-width: $size;

			&.-screenreader {
				padding-left: 0;
			}

			&:before {
				overflow: hidden;
				content: '';
				z-index: $midground;
				position: absolute;
				top: $checkbox-border-width;
				left: $checkbox-border-width;
				width: $size;
				height: $size;
				border: $checkbox-border-width solid $enabled-color;
				border-radius: $checkbox-border-width;
				background-color: $enabled-color;

				&:hover {
					border-color: $enabled-color;
				}
			}

			&:after {
				@include icon-for('checkmark');
				z-index: $midground;
				position: absolute;
				top: (($size/3) - $checkbox-border-width)/1.75;
				left: (($size/2) + $checkbox-border-width)/2;
				font-size: $size * 0.65;
				background-color: $enabled-color;
				color: white;
				@include media($mobile) {
					top: (($size/2.5) - $checkbox-border-width)/2;
				}
			}
		}

		& :focus {
			& + label:before {
				@include checkbox-focus-outline;
			}
		}
	}

	&[type='checkbox']:not(:checked):not(.check-box) + label:before {
		background-color: white;
	}

	&[type='checkbox']:disabled:checked:not(.check-box) + label:before {
		border: $checkbox-border-width solid $button-disable-color;
		background-color: $button-disable-color;
	}

	&[type='checkbox']:disabled:not(:checked):not(.check-box) + label:before,
	&[type='checkbox']:disabled:checked:not(.check-box) + label:before {
		border: $checkbox-border-width solid $button-disable-color;
	}

	&[type='checkbox']:disabled:checked:not(.check-box) + label:after {
		background-color: $button-disable-color;
		color: white;
	}

	&[type='checkbox']:disabled:not(.check-box) + label {
		cursor: not-allowed;
	}

	&[type='checkbox']:not(:checked):not(.check-box) + label:after {
		opacity: 0;
	}

	&[type='checkbox']:checked:not(.check-box) + label:after {
		opacity: 1;
	}
}
