@charset "UTF-8";
// Scaling Variables
$golden: 1.618;
$minor-second: 1.067;
$major-second: 1.125;
$minor-third: 1.2;
$major-third: 1.25;
$perfect-fourth: 1.333;
$augmented-fourth: 1.414;
$perfect-fifth: 1.5;
$minor-sixth: 1.6;
$major-sixth: 1.667;
$minor-seventh: 1.778;
$major-seventh: 1.875;
$octave: 2;
$major-tenth: 2.5;
$major-eleventh: 2.667;
$major-twelfth: 3;
$double-octave: 4;
$em-base: 16px !default;
//function modular scale has to be before the grid vars
@function modular-scale(
	$increment,
	$value: $modular-scale-base,
	$ratio: $modular-scale-ratio
) {
	$v1: nth($value, 1);
	$v2: nth($value, length($value));
	$value: $v1;

	// scale $v2 to just above $v1
	@while $v2 > $v1 {
		$v2: ($v2 / $ratio); // will be off-by-1
	}
	@while $v2 < $v1 {
		$v2: ($v2 * $ratio); // will fix off-by-1
	}

	// check AFTER scaling $v2 to prevent double-counting corner-case
	$double-stranded: $v2 > $v1;

	@if $increment > 0 {
		@for $i from 1 through $increment {
			@if $double-stranded and ($v1 * $ratio) > $v2 {
				$value: $v2;
				$v2: ($v2 * $ratio);
			} @else {
				$v1: ($v1 * $ratio);
				$value: $v1;
			}
		}
	}

	@if $increment < 0 {
		// adjust $v2 to just below $v1
		@if $double-stranded {
			$v2: ($v2 / $ratio);
		}

		@for $i from $increment through -1 {
			@if $double-stranded and ($v1 / $ratio) < $v2 {
				$value: $v2;
				$v2: ($v2 / $ratio);
			} @else {
				$v1: ($v1 / $ratio);
				$value: $v1;
			}
		}
	}

	@return $value;
}
$column: modular-scale(3, 1em, $golden) !default;
$gutter: modular-scale(1, 1em, $golden) !default;
$grid-columns: 12 !default;
$max-width: em(1088) !default;
$border-box-sizing: true !default;
$default-feature: min-width;
$default-layout-direction: LTR !default;
$direction: $default-layout-direction;
$layout-direction: $direction !global;
$container-display-table: false !default;
$modular-scale-ratio: $perfect-fourth !default;
$modular-scale-base: em($em-base) !default;
$ease-in-quad: cubic-bezier(0.55, 0.085, 0.68, 0.53);
$ease-in-cubic: cubic-bezier(0.55, 0.055, 0.675, 0.19);
$ease-in-quart: cubic-bezier(0.895, 0.03, 0.685, 0.22);
$ease-in-quint: cubic-bezier(0.755, 0.05, 0.855, 0.06);
$ease-in-sine: cubic-bezier(0.47, 0, 0.745, 0.715);
$ease-in-expo: cubic-bezier(0.95, 0.05, 0.795, 0.035);
$ease-in-circ: cubic-bezier(0.6, 0.04, 0.98, 0.335);
$ease-in-back: cubic-bezier(0.6, -0.28, 0.735, 0.045);
$ease-out-quad: cubic-bezier(0.25, 0.46, 0.45, 0.94);
$ease-out-cubic: cubic-bezier(0.215, 0.61, 0.355, 1);
$ease-out-quart: cubic-bezier(0.165, 0.84, 0.44, 1);
$ease-out-quint: cubic-bezier(0.23, 1, 0.32, 1);
$ease-out-sine: cubic-bezier(0.39, 0.575, 0.565, 1);
$ease-out-expo: cubic-bezier(0.19, 1, 0.22, 1);
$ease-out-circ: cubic-bezier(0.075, 0.82, 0.165, 1);
$ease-out-back: cubic-bezier(0.175, 0.885, 0.32, 1.275);
$ease-in-out-quad: cubic-bezier(0.455, 0.03, 0.515, 0.955);
$ease-in-out-cubic: cubic-bezier(0.645, 0.045, 0.355, 1);
$ease-in-out-quart: cubic-bezier(0.77, 0, 0.175, 1);
$ease-in-out-quint: cubic-bezier(0.86, 0, 0.07, 1);
$ease-in-out-sine: cubic-bezier(0.445, 0.05, 0.55, 0.95);
$ease-in-out-expo: cubic-bezier(1, 0, 0, 1);
$ease-in-out-circ: cubic-bezier(0.785, 0.135, 0.15, 0.86);
$ease-in-out-back: cubic-bezier(0.68, -0.55, 0.265, 1.55);
/// Global variables to enable or disable vendor prefixes
$prefix-for-webkit: true !default;
$prefix-for-mozilla: true !default;
$prefix-for-microsoft: true !default;
$prefix-for-opera: true !default;
$prefix-for-spec: true !default;
$visual-grid: false !default;
$visual-grid-color: #eee !default;
$visual-grid-index: back !default;
$visual-grid-opacity: 0.4 !default;
$visual-grid-breakpoints: () !default;
$parent-columns: $grid-columns !default;
$fg-column: $column;
$fg-gutter: $gutter;
$fg-max-columns: $grid-columns;
$container-display-table: false !default;
$layout-direction: LTR !default;

@function is-not($value) {
	@return if($value, false, true);
}

// Checks if a number is even
@function is-even($int) {
	@return $int % 2 == 0;
}

// Checks if an element belongs to a list or not
@function belongs-to($tested-item, $list) {
	@return is-not(not-belongs-to($tested-item, $list));
}

@function not-belongs-to($tested-item, $list) {
	@return is-not(index($list, $tested-item));
}

// Contains display value
@function contains-display-value($query) {
	@return belongs-to(table, $query) or belongs-to(block, $query) or
		belongs-to(inline-block, $query) or belongs-to(inline, $query);
}

// Parses the first argument of span-columns()
@function container-span($span: $span) {
	@if length($span) == 3 {
		$container-columns: nth($span, 3);
		@return $container-columns;
	} @else if length($span) == 2 {
		$container-columns: nth($span, 2);
		@return $container-columns;
	}

	@return $grid-columns;
}

@function container-shift($shift: $shift) {
	$parent-columns: $grid-columns !default !global;

	@if length($shift) == 3 {
		$container-columns: nth($shift, 3);
		@return $container-columns;
	} @else if length($shift) == 2 {
		$container-columns: nth($shift, 2);
		@return $container-columns;
	}

	@return $parent-columns;
}

// Generates a striped background
@function gradient-stops($grid-columns, $color: $visual-grid-color) {
	$transparent: transparent;

	// $column-width: flex-grid(1, $grid-columns);
	$gutter-width: flex-gutter($grid-columns);
	$column-offset: $column-width;

	$values: ($transparent 0, $color 0);

	@for $i from 1 to $grid-columns * 2 {
		@if is-even($i) {
			$values: append($values, $transparent $column-offset, comma);
			$values: append($values, $color $column-offset, comma);
			$column-offset: $column-offset + $column-width;
		} @else {
			$values: append($values, $color $column-offset, comma);
			$values: append($values, $transparent $column-offset, comma);
			$column-offset: $column-offset + $gutter-width;
		}
	}

	@return $values;
}

// Layout direction
@function get-direction($layout, $default) {
	$direction: null;

	@if to-upper-case($layout) == 'LTR' or to-upper-case($layout) == 'RTL' {
		$direction: direction-from-layout($layout);
	} @else {
		$direction: direction-from-layout($default);
	}

	@return $direction;
}

@function direction-from-layout($layout) {
	$direction: null;

	@if to-upper-case($layout) == 'LTR' {
		$direction: right;
	} @else {
		$direction: left;
	}

	@return $direction;
}

@function get-opposite-direction($direction) {
	$opposite-direction: left;

	@if $direction == 'left' {
		$opposite-direction: right;
	}

	@return $opposite-direction;
}

// @function flex-grid($columns, $container-columns: $fg-max-columns) {
//   $width: $columns * $fg-column + ($columns - 1) * $fg-gutter;
//   $container-width: $container-columns * $fg-column + ($container-columns - 1) * $fg-gutter;
//   @return percentage($width / $container-width);
// }

@function flex-gutter(
	$container-columns: $fg-max-columns,
	$gutter: $fg-gutter
) {
	$container-width: $container-columns * $fg-column + ($container-columns - 1) *
		$fg-gutter;
	@return percentage($gutter / $container-width);
}

@function grid-width($n) {
	@return $n * $gw-column + ($n - 1) * $gw-gutter;
}

@function get-parent-columns($columns) {
	@if $columns != $grid-columns {
		$parent-columns: $columns !global;
	} @else {
		$parent-columns: $grid-columns !global;
	}

	@return $parent-columns;
}

@function is-display-table($container-is-display-table, $display) {
	@return $container-is-display-table == true or $display == table;
}

// px to EM
@function rem($pxval) {
	@if $output-bourbon-deprecation-warnings==true {
		@warn "[Bourbon] [Deprecation] `rem` is deprecated and will be " + "removed in 5.0.0.";
	}
	$user-deprecation-warnings-setting: $output-bourbon-deprecation-warnings;
	$output-bourbon-deprecation-warnings: false !global;
	@if not unitless($pxval) {
		$pxval: strip-units($pxval);
	}
	$base: $em-base;
	@if not unitless($base) {
		$base: strip-units($base);
	}
	$output-bourbon-deprecation-warnings: $user-deprecation-warnings-setting !global;
	@return ($pxval / $base) * 1rem;
}

// Grid Mixins
//Fill Parent
@mixin fill-parent() {
	width: 100%;
	@if $border-box-sizing==false {
		box-sizing: border-box;
	}
}

//Span-columns
@mixin span-columns($span: $columns of $container-columns, $display: block) {
	$columns: nth($span, 1);
	$container-columns: container-span($span);
	$parent-columns: get-parent-columns($container-columns) !global;
	$direction: get-direction($layout-direction, $default-layout-direction);
	$opposite-direction: get-opposite-direction($direction);
	$display-table: is-display-table($container-display-table, $display);
	@if $display-table {
		display: table-cell;
		width: percentage($columns / 12);
	} @else {
		float: #{$opposite-direction};
		@if $display !=no-display {
			display: block;
		}
		@if $display==collapse {
			@include -neat-warn(
				"The 'collapse' argument will be deprecated. Use 'block-collapse' instead."
			);
		}
		@if $display==collapse or $display==block-collapse {
			// width: flex-grid($columns, $container-columns) +
			// 	flex-gutter($container-columns);
			&:last-child {
				// width: flex-grid($columns, $container-columns);
			}
		} @else {
			margin-#{$direction}: flex-gutter($container-columns);
			// width: flex-grid($columns, $container-columns);
			&:last-child {
				margin-#{$direction}: 0;
			}
		}
	}
}

//prefixer
////// A mixin for generating vendor prefixes on non-standardized properties.
/// @param {String} $property
///   Property to prefix
///
/// @param {*} $value
///   Value to use
///
/// @param {List} $prefixes
///   Prefixes to define
///
/// @example scss - Usage
///   .element {
///     @include prefixer(border-radius, 10px, webkit ms spec);
///   }
///
/// @example css - CSS Output
///   .element {
///     -webkit-border-radius: 10px;
///     -moz-border-radius: 10px;
///     border-radius: 10px;
///   }
///
/// @require {variable} $prefix-for-webkit
/// @require {variable} $prefix-for-mozilla
/// @require {variable} $prefix-for-microsoft
/// @require {variable} $prefix-for-opera
/// @require {variable} $prefix-for-spec

@mixin prefixer($property, $value, $prefixes) {
	@each $prefix in $prefixes {
		@if $prefix == webkit {
			@if $prefix-for-webkit {
				-webkit-#{$property}: $value;
			}
		} @else if $prefix == moz {
			@if $prefix-for-mozilla {
				-moz-#{$property}: $value;
			}
		} @else if $prefix == ms {
			@if $prefix-for-microsoft {
				-ms-#{$property}: $value;
			}
		} @else if $prefix == o {
			@if $prefix-for-opera {
				-o-#{$property}: $value;
			}
		} @else if $prefix == spec {
			@if $prefix-for-spec {
				#{$property}: $value;
			}
		} @else {
			@warn "Unrecognized prefix: #{$prefix}";
		}
	}
}

@mixin disable-prefix-for-all() {
	$prefix-for-webkit: false !global;
	$prefix-for-mozilla: false !global;
	$prefix-for-microsoft: false !global;
	$prefix-for-opera: false !global;
	$prefix-for-spec: false !global;
}

//Shift
@mixin shift($n-columns: 1) {
	@include shift-in-context($n-columns);
}

@mixin shift-in-context($shift: $columns of $container-columns) {
	$n-columns: nth($shift, 1);
	$parent-columns: container-shift($shift) !global;
	$direction: get-direction($layout-direction, $default-layout-direction);
	$opposite-direction: get-opposite-direction($direction);
	// margin-#{$opposite-direction}: $n-columns * flex-grid(1, $parent-columns) +
	// $n-columns * flex-gutter($parent-columns);
	// Reset nesting context
	$parent-columns: $grid-columns !global;
}
@mixin clearfix {
	&::after {
		clear: both;
		content: '';
		display: table;
	}
}
//Row
@mixin row($display: default, $direction: $default-layout-direction) {
	@if $direction !=$default-layout-direction {
		@include -neat-warn(
			'The $direction argument will be deprecated in future versions in favor of the direction(){...} mixin.'
		);
	}
	$layout-direction: $direction !global;
	@if $display !=default {
		@include -neat-warn(
			'The $display argument will be deprecated in future versions in favor of the display(){...} mixin.'
		);
	}
	@if $display==table {
		display: table;
		@include fill-parent;
		table-layout: fixed;
		$container-display-table: true !global;
	} @else {
		@include clearfix;
		display: block;
		$container-display-table: false !global;
	}
}

//Outer Container
@mixin outer-container($local-max-width: $max-width) {
	@include clearfix;
	max-width: $local-max-width;
	margin: {
		left: auto;
		right: auto;
	}
}
//new breakpoint
@function new-breakpoint(
	$query: $feature $value $columns,
	$total-columns: $grid-columns
) {
	@if length($query) == 1 {
		$query: $default-feature nth($query, 1) $total-columns;
	} @else if is-even(length($query)) {
		$query: append($query, $total-columns);
	}

	@if is-not(belongs-to($query, $visual-grid-breakpoints)) {
		$visual-grid-breakpoints: append(
			$visual-grid-breakpoints,
			$query,
			comma
		) !global;
	}

	@return $query;
}

//Omega
@mixin omega($query: block, $direction: default) {
	$table: belongs-to(table, $query);
	$auto: belongs-to(auto, $query);
	@if $direction !=default {
		@include -neat-warn(
			'The omega mixin will no longer take a $direction argument. To change the layout direction, use the direction(){...} mixin.'
		);
	} @else {
		$direction: get-direction($layout-direction, $default-layout-direction);
	}
	@if length($query) ==1 {
		@if $auto {
			&:last-child {
				margin-#{$direction}: 0;
			}
		} @else if contains-display-value($query) and $table==false {
			margin-#{$direction}: 0;
		} @else {
			@include nth-child($query, $direction);
		}
	} @else if length($query) ==2 {
		@if $auto {
			&:last-child {
				margin-#{$direction}: 0;
			}
		} @else {
			@include nth-child(nth($query, 1), $direction);
		}
	} @else {
		@include -neat-warn('Too many arguments passed to the omega() mixin.');
	}
}

@mixin nth-child($query, $direction) {
	$opposite-direction: get-opposite-direction($direction);
	&:nth-child(#{$query}) {
		margin-#{$direction}: 0;
	}
	@if type-of($query) ==number and unit($query) == 'n' {
		&:nth-child(#{$query} + 1) {
			clear: $opposite-direction;
		}
	}
}

// direction Mixin
@mixin direction-context($direction: left-to-right) {
	$scope-direction: $layout-direction;
	@if to-lower-case($direction) == 'left-to-right' {
		$layout-direction: LTR !global;
	} @else if to-lower-case($direction) == 'right-to-left' {
		$layout-direction: RTL !global;
	}
	@content;
	$layout-direction: $scope-direction !global;
}

// Responsive breakpoint Mixin
@mixin media($query: $feature $value $columns, $total-columns: $grid-columns) {
	@if length($query) ==1 {
		@media screen and ($default-feature: nth($query, 1)) {
			$default-grid-columns: $grid-columns;
			$grid-columns: $total-columns !global;
			@content;
			$grid-columns: $default-grid-columns !global;
		}
	} @else {
		$loop-to: length($query);
		$media-query: 'screen and ';
		$default-grid-columns: $grid-columns;
		$grid-columns: $total-columns !global;
		@if is-not(is-even(length($query))) {
			$grid-columns: nth($query, $loop-to) !global;
			$loop-to: $loop-to - 1;
		}
		$i: 1;
		@while $i <=$loop-to {
			$media-query: $media-query +
				'(' +
				nth($query, $i) +
				': ' +
				nth($query, $i + 1) +
				') ';
			@if ($i + 1) !=$loop-to {
				$media-query: $media-query + 'and ';
			}
			$i: $i + 2;
		}
		@media #{$media-query} {
			@content;
			$grid-columns: $default-grid-columns !global;
		}
	}
}

//Timing Functions
@mixin transition($properties...) {
	// Fix for vendor-prefix transform property
	$needs-prefixes: false;
	$webkit: ();
	$moz: ();
	$spec: ();
	// Create lists for vendor-prefixed transform
	@each $list in $properties {
		@if nth($list, 1) == 'transform' {
			$needs-prefixes: true;
			$list1: -webkit-transform;
			$list2: -moz-transform;
			$list3: ();
			@each $var in $list {
				$list3: join($list3, $var);
				@if $var != 'transform' {
					$list1: join($list1, $var);
					$list2: join($list2, $var);
				}
			}
			$webkit: append($webkit, $list1);
			$moz: append($moz, $list2);
			$spec: append($spec, $list3);
		} @else {
			$webkit: append($webkit, $list, comma);
			$moz: append($moz, $list, comma);
			$spec: append($spec, $list, comma);
		}
	}
	@if $needs-prefixes {
		-webkit-transition: $webkit;
		-moz-transition: $moz;
		transition: $spec;
	} @else {
		@if length($properties) >=1 {
			@include prefixer(transition, $properties, webkit moz spec);
		} @else {
			$properties: all 0.15s ease-out 0s;
			@include prefixer(transition, $properties, webkit moz spec);
		}
	}
}

@mixin transition-property($properties...) {
	-webkit-transition-property: transition-property-names($properties, 'webkit');
	-moz-transition-property: transition-property-names($properties, 'moz');
	transition-property: transition-property-names($properties, false);
}

@mixin transition-duration($times...) {
	@include prefixer(transition-duration, $times, webkit moz spec);
}

@mixin transition-timing-function($motions...) {
	// ease | linear | ease-in | ease-out | ease-in-out | cubic-bezier()
	@include prefixer(transition-timing-function, $motions, webkit moz spec);
}

@mixin transition-delay($times...) {
	@include prefixer(transition-delay, $times, webkit moz spec);
}

@function unpack($shorthand) {
	@if length($shorthand) == 1 {
		@return nth($shorthand, 1) nth($shorthand, 1) nth($shorthand, 1)
			nth($shorthand, 1);
	} @else if length($shorthand) == 2 {
		@return nth($shorthand, 1) nth($shorthand, 2) nth($shorthand, 1)
			nth($shorthand, 2);
	} @else if length($shorthand) == 3 {
		@return nth($shorthand, 1) nth($shorthand, 2) nth($shorthand, 3)
			nth($shorthand, 2);
	} @else {
		@return $shorthand;
	}
}

@function _is-length($value) {
	@return type-of($value) != 'null' and
		(
			str-slice($value + '', 1, 4) == 'calc' or
				index(auto inherit initial 0, $value) or
				(type-of($value) == 'number' and not(unitless($value)))
		);
}

@mixin position($position: relative, $coordinates: null null null null) {
	@if type-of($position) == list {
		$coordinates: $position;
		$position: relative;
	}

	$coordinates: unpack($coordinates);

	$offsets: (
		top: nth($coordinates, 1),
		right: nth($coordinates, 2),
		bottom: nth($coordinates, 3),
		left: nth($coordinates, 4),
	);

	position: $position;

	@each $offset, $value in $offsets {
		@if is-length($value) {
			#{$offset}: $value;
		}
	}
}

//Size Mixin
@mixin size($value) {
	$width: nth($value, 1);
	$height: $width;
	@if length($value) > 1 {
		$height: nth($value, 2);
	}
	@if is-size($height) {
		height: $height;
	} @else {
		@warn "`#{$height}` is not a valid length for the `$height` parameter in the `size` mixin.";
	}
	@if is-size($width) {
		width: $width;
	} @else {
		@warn "`#{$width}` is not a valid length for the `$width` parameter in the `size` mixin.";
	}
}

//
@mixin pad($padding: flex-gutter()) {
	$padding-list: null;
	@each $value in $padding {
		$value: if($value == 'default', flex-gutter(), $value);
		$padding-list: join($padding-list, $value);
	}
	padding: $padding-list;
}
// CSS3 Flexible Box Model and property defaults
// Unified attributes for 2009, 2011, and 2012 flavours.

// 2009 - display (box | inline-box)
// 2011 - display (flexbox | inline-flexbox)
// 2012 - display (flex | inline-flex)
@mixin display($value) {
	// flex | inline-flex
	@if $value == 'flex' {
		// 2009
		display: -webkit-box;
		display: -moz-box;
		display: box;

		// 2012
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flexbox; // 2011 (IE 10)
		display: flex;
	} @else if $value == 'inline-flex' {
		display: -webkit-inline-box;
		display: -moz-inline-box;
		display: inline-box;

		display: -webkit-inline-flex;
		display: -moz-inline-flex;
		display: -ms-inline-flexbox;
		display: inline-flex;
	} @else {
		display: $value;
	}
}

// 2012 - flex-grow (number)
@mixin flex-grow($number: 0) {
	@include prefixer(flex-grow, $number, webkit moz spec);
	-ms-flex-positive: $number;
}

// 2012 - flex-shrink (number)
@mixin flex-shrink($number: 1) {
	@include prefixer(flex-shrink, $number, webkit moz spec);
	-ms-flex-negative: $number;
}

// 2012 - flex-basis (number)
@mixin flex-basis($width: auto) {
	@include prefixer(flex-basis, $width, webkit moz spec);
	-ms-flex-preferred-size: $width;
}
