$feature-icon-desktop-size: 64px;
$feature-icon-desktop-size: 64px;
$circle-icon-small-mobile-size: 16px;
$circle-icon-small-desktop-size: 28px;
$circle-icon-small-size: 16px;
$circle-icon-small-font-size: 10px;
$circle-icon-large-size: 64px;
$feature-icon-mobile-size: 50px;
$icon-mobile-size: 30px;
$compact-icon-size: 40px;
$tooltip-icon-size: 15px;
$tooltip-icon-font-size: 14px;
$default-icon-style: "solid";
$desktop-icon-border-size: 3px;
$mobile-icon-border-size: 2px;
$icon-line-height: 1.75;
$icon-size-with-circle: 34px;
$large-font-size: 28px;

$neutral-color: color("dolphin");
$transparent: color("transparent");


.icon-grid {
  max-width: 1240px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
  flex-wrap: wrap;
  text-align: center;
  .row {
    width: 100%;
  }
  .icon-example {
    margin: 15px;
    width: 17%;
    float: left;
    height: 120px;
    .-name {
      display: block;
      padding-top: 10px
    }
  }
}

@mixin icon-for($icon-name){
  @extend .icon-#{$icon-name}:before;
  font-family: 'kp-icons';
}
@mixin icon-color($color) {
  color: color($color);
}

%centered-icon {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  line-height: $icon-line-height;
  @-moz-document url-prefix() {
    line-height: normal;
  }
  &:before {
    vertical-align: -moz-middle-with-baseline;
  }
}

@mixin flex-position {
  @include cross-browser(display, inline-flex);
  @include align-center;
  display: -ms-inline-flexbox;
  line-height: 1;
  padding: 0;
  margin: 0;
}

%rounded-icon {
  width: $feature-icon-mobile-size;
  height: $feature-icon-mobile-size;
  font-size: $feature-icon-mobile-size/2;
  border-radius: 50%;
  @include media($desktop) {
    width: $feature-icon-desktop-size;
    height: $feature-icon-desktop-size;
    font-size: $feature-icon-desktop-size/2;
  }
}

%rounded-icon--small {
  @include size($circle-icon-small-desktop-size);
  border-radius: 50%;
}

@mixin feature-icon($color, $style: $default-icon-style) {
  @extend %rounded-icon;
  @extend %centered-icon;
  background-color: color($color);
  color: set-text-color(color($color));
  @if $style=="hollow" {
    color: color($color);
    background-color: transparent;
    border: $mobile-icon-border-size solid color($color);
    @include media($desktop) {
      border: $desktop-icon-border-size solid color($color);
    }
  }
  @if $style=="blue-background" {
    color: color($color);
    background-color: color("pale-blue");
    display: flex;
    align-items: center;
    justify-content: center;
  }
  @if $style=="compact" {
    width: $compact-icon-size;
    height: $compact-icon-size;
    font-size: $feature-icon-desktop-size/2;
  }
}

@mixin icon-style($color, $size) {
  @include size($size);
  color: color($color);
  font-size: $size;
  line-height: $size;
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin primary-icon($color, $size) {
  @extend %rounded-icon;
  @extend %centered-icon;
  background-color: color($transparent);
  color: color($color);
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin icon-primary($color, $size) {
  @extend %rounded-icon;
  @extend %centered-icon;
  background-color: color($color);
  color: #fff;
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin icon-primary-small($color, $size) {
  @extend %rounded-icon--small;
  @extend %centered-icon;
  background-color: color($color);
  color: #fff;
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin icon-secondary($color, $size) {
  @extend %rounded-icon;
  @extend %centered-icon;
  color: color($color);
  font-size: $icon-size-with-circle;
  background-color: color("pale-blue");
  display: inline-flex;
  align-items: center;
  justify-content: center;
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin icon-neutral($size) {
  @extend %rounded-icon;
  color: $neutral-color;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  font-size: $size;
  border: $mobile-icon-border-size solid $neutral-color;
  @include media($desktop) {
    border: $desktop-icon-border-size solid $neutral-color;
    font-size: $size;
  }
}

@mixin icon-mobile ($color) {
  color: color($color);
  font-size: $icon-mobile-size;
}

@mixin icon-utility {
  color: $neutral-color;
  font-size: $icon-mobile-size;
}
@mixin icon_circle-small {
  @include size($circle-icon-small-size);
}

@mixin icon_circle-large {
  width: $circle-icon-large-size;
  height: $circle-icon-large-size;
}


//tooltip icon mixin
@mixin tooltip-icon($color, $style: $default-icon-style) {
  @extend %centered-icon;
  background-color: color($color);
  color: set-text-color(color($color));
  width: $tooltip-icon-size;
  height: $tooltip-icon-size;
  font-size: $tooltip-icon-font-size;
  border-radius: 50%;
  @if $style=="hollow" {
    color: color($color);
    background-color: transparent;
    border: $mobile-icon-border-size solid color($color);
  }
}

@at-root {
  @keyframes fa-spin {
    0% {
      transform: rotate(0deg);
    }
    100% {
      transform: rotate(359deg);
    }
  }
}
