@mixin icon-for($icon-name){
  @extend .icon-#{$icon-name}, :before;
  font-family: 'kp-icons';
}
@mixin icon-color($color) {
  color: color($color);
}

@mixin centered-icon {
  display: inline-block;
  text-align: center;
  vertical-align: middle;
  line-height: $icon-line-height;
  @-moz-document url-prefix() {
    line-height: normal;
  }
  &:before {
    vertical-align: -moz-middle-with-baseline;
  }
}

@mixin icon-sm {
  font-size: $large-font-size;
  line-height: $large-font-size;
}

@mixin icon-md {
  font-size: 2.25em;
  line-height: 2.25em;
}

@mixin icon-lg {
  font-size: $feature-icon-desktop-size;
  line-height: $feature-icon-desktop-size;
}

@mixin rounded-icon {
  width: $feature-icon-mobile-size;
  height: $feature-icon-mobile-size;
  font-size: $feature-icon-mobile-size/2;
  border-radius: 50%;
  @include media($desktop) {
    width: $feature-icon-desktop-size;
    height: $feature-icon-desktop-size;
    font-size: $feature-icon-desktop-size/2;
  }
}

@mixin feature-icon($color, $style: $default-icon-style) {
  @include rounded-icon;
  @include centered-icon;
  background-color: color($color);
  color: set-text-color(color($color));
  @if $style == "hollow" {
    color: color($color);
    background-color: transparent;
    border: $mobile-icon-border-size solid color($color);
    @include media($desktop) {
      border: $desktop-icon-border-size solid color($color);
    }
  }
  @if $style == "blue-background" {
    color: color($color);
    background-color: color("pale-blue");
    display: inline-flex;
    align-items: center;
    justify-content: center;
  }
  @if $style == "compact" {
    width: $compact-icon-size;
    height: $compact-icon-size;
    font-size: $feature-icon-desktop-size/2;
  }
}
@mixin icon-style($color, $size) {
  @include size($size);
  color: color($color);
  font-size: $size;
  line-height: $size;
}
@mixin icon-primary($color, $size) {
  @include rounded-icon;
  @include centered-icon;
  background-color: color($color);
  color: #fff;
  @include media($desktop) {
    font-size: $size;
  }
}
@mixin icon-primary-small($color, $size) {
  @extend %rounded-icon--small;
  @extend %centered-icon;
  background-color: color($color);
  color: #fff;
  @include media($desktop) {
    font-size: $size;
  }
}
@mixin icon-secondary($color, $size) {
  @include rounded-icon;
  @include centered-icon;
  color: color($color);
  font-size: $icon-size-with-circle;
  background-color: color("pale-blue");
  display: inline-flex;
  align-items: center;
  justify-content: center;
  @include media($desktop) {
    font-size: $size;
  }
}

@mixin icon-neutral($size) {
  @include rounded-icon;
  color: $neutral-color;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  background-color: transparent;
  font-size: $size;
  border: $mobile-icon-border-size solid $neutral-color;
  @include media($desktop) {
    border: $desktop-icon-border-size solid $neutral-color;
    font-size: $size;
  }
}
@mixin icon-mobile ($color) {
  color: color($color);
  font-size: $icon-mobile-size;
}
@mixin icon-utility {
  color: $neutral-color;
  font-size: $icon-mobile-size;
}
//tooltip icon mixin
@mixin tooltip-icon($color, $style: $default-icon-style) {
  @include centered-icon;
  background-color: color($color);
  color: set-text-color(color($color));
  width: $tooltip-icon-size;
  height: $tooltip-icon-size;
  font-size: $tooltip-icon-font-size;
  border-radius: 50%;
  @if $style == "hollow" {
    color: color($color);
    background-color: transparent;
    border: $mobile-icon-border-size solid color($color);
  }

}
