$numColumns: 12;
$gutterWidth: 32px;
$large-desktop-width: 1280px;
$smaller-mobile: 400px;
$desktop-width-max: 1204px;
$gutter-desktop: 38px;
$gutter-desktop-small: 35px;
$gutter-tablet: 28px;
$gutter-mobile: 16px;
$mobile-max: new-breakpoint(max-width $mobile-max, 12);
$direction: get-direction($layout-direction, $default-layout-direction);

.rows {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-evenly;
  width: 100%;
  flex: 1;
  @for $i from 1 through $numColumns {
    @if $i==12 {
      flex: 1;
      margin-right: 0;
    }
  
    @if $i from 1 to 11 {
      flex-basis: (100 / ($numColumns/ $i)) * 1%;
      margin-right: 32px;
  
    }
    .columns-#{$i} {
      flex-basis: (100 / ($numColumns/ $i) ) * 1%;
      margin-right: 32px;
      &:last-child() {
        margin-right: 0;
    }
    @include media($desktop-small) {
      @if $i==12 {
        flex-basis: 954px;
      }
  
      @if $i from 1 to 11 {
        flex-basis: (100 / ($numColumns/ $i)) * 1%;
        margin-right: 30px;
      }
    }
    @include media($tablet) {
      @if $i==12 {
        flex-basis: 954px;
      }
  
      @if $i from 1 to 11 {
        flex-basis: (100 / ($numColumns/ $i)) * 1%;
        margin-right: 20px;
      }
    }
    @include media($mobile-max) {
      @if $i from 4 to 12 {
          flex: 1;
      }
      @if $i==2 or $i==3 {
        flex-basis: (100 / ($numColumns/ $i)) * 1%;
        margin-right: 16px;
      }
      @if $i==1 {
        flex-basis: (100 / ($numColumns/ $i)) * 1%;
        margin-right: 16px;
      }
    }
  }
}
}

@mixin columns($i) {
  @if $i==12 or $i==6 or $i==4 or $i==3 or $i==2 or $i==1 {
   grid-column: span  $i;
   -ms-grid-column-span:  $i;
   margin-right: 0;
  }
 }

@supports  (display: grid) {
  .rows{
    display: grid;
    grid-template-columns: repeat($numColumns, 1fr); 
    grid-gap: 0 32px;
    width: 100%;

    @for $i from 1 through $numColumns {
      .columns-#{$i}{
        grid-column: span  $i;
        -ms-grid-column-span:  $i;
        margin-right: 0;
      }
    }
  }
  
  @include media($desktop-small) {
    .rows{
      grid-gap: 0 30px;
    }
  }

  @include media($tablet) {
    .rows{
     grid-gap: 0 20px;
    }
  }

  @include media($mobile-max){
    .rows{
      grid-template-columns: repeat(4, 1fr);
      grid-gap: 0 16px;
      @for $i from 1 through $numColumns {
        grid-column: span 4;
        -ms-grid-column-span: 4;
        margin-right: 0;
  
        @if $i from 1 to 3 {
          grid-column: span $i;
          -ms-grid-column-span: $i;
          margin-right: 0;
        }
      }
    }
  }
}

@mixin newGrid__container {
  @include clearfix;
  margin-right: $gutter-desktop;
  margin-left: $gutter-desktop;
  
  @include media($desktop-large) {
    margin-right: auto;
    margin-left: auto;
    padding: 0 $gutter-desktop;
    max-width: $large-desktop-width;
    }
  
  @include media($desktop-small) {
    margin-right: $gutter-desktop-small;
    margin-left: $gutter-desktop-small;
  }
  
  @include media($tablet) {
    margin-right: $gutter-tablet;
    margin-left: $gutter-tablet;
  }
  
  @include media($mobile) {
    margin-right: $gutter-mobile;
    margin-left: $gutter-mobile;
  }
}

.newGrid_container {
  @include newGrid__container;
}