// Last updated on 2019-06-20 by Eric Yeh for SG v3.9.19

$check-box-default-color: color("dolphin");
$check-box-focus-color: rgba(123, 183, 246, 0.8);
$check-box-disable-color: color("graphite-dark");
$check-box-disable-text: color("dolphin");
$check-box-border-width: 2px;
$check-box-font-variation-1: 16px;
$check-box-size-variation-1: 18px;
$check-box-checkmark-size-variation-1: 14px;
$check-box-checkmark-position-top-variation-1: 2px;
$check-box-checkmark-position-left-variation-1: 2px;
$check-box-space-right-variation-1: 10px;
$check-box-space-bottom-variation-1: 15px;
$check-box-font-variation-2: 20px;
$check-box-size-variation-2: 22px;
$check-box-checkmark-size-variation-2: 15px;
$check-box-checkmark-position-top-variation-2: 1px;
$check-box-checkmark-position-left-variation-2: 4px;
$check-box-space-right-variation-2: 15px;
$check-box-space-bottom-variation-2: 20px;
$check-box-font-variation-3: 28px;
$check-box-size-variation-3: 30px;
$check-box-checkmark-size-variation-3: 22px;
$check-box-checkmark-position-top-variation-3: 1px;
$check-box-checkmark-position-left-variation-3: 4.5px;
$check-box-space-right-variation-3: 15px;
$check-box-space-bottom-variation-3: 20px;

$checkbox-toggle-switch-color-off: color("dolphin");
$checkbox-toggle-switch-color-on: color("kp-blue");
$checkbox-toggle-switch-transition-speed: 0.1s;


@mixin checkbox-focus-outline {
  border: $check-box-border-width solid $check-box-default-color;
  @include firefox-styling;
  -webkit-box-shadow: 0 0 0 2pt $check-box-focus-color;
  -moz-box-shadow: 0 0 0 2pt $check-box-focus-color;
  box-shadow: 0 0 0 2pt $check-box-focus-color;
  @include ie-styling;
  outline: 1px dotted $check-box-disable-color;
}

// ADA compliant checkbox
// Note: "@mixin check-box" AND "@mixin check-box-class" contains the same code logic as of 2019-06-01. "@mixin check-box-class" is created temporarily in order to reconcile the issues encountered when merging "@import checkboxes-lib" in checkboxes.scss
@mixin check-box($check-box-font: $check-box-font-variation-1, $check-box-size: $check-box-size-variation-1, $check-box-checkmark-size: $check-box-checkmark-size-variation-1, $check-box-checkmark-position-top: $check-box-checkmark-position-top-variation-1, $check-box-checkmark-position-left: $check-box-checkmark-position-left-variation-1, $check-box-space-right: $check-box-space-right-variation-1, $check-box-space-bottom: $check-box-space-bottom-variation-1) {
  // hide the real checkbox
  position: absolute;
  margin-top: $check-box-space-bottom/2;
  opacity: 0;

  + label {
    display: inline-block;
    position: relative;
    margin: $check-box-space-bottom/2 0;
    padding: $check-box-border-width 0 0 $check-box-size + $check-box-space-right - $check-box-border-width;
    min-height: $check-box-size;
    font-size: $check-box-font;
    line-height: 1em;
    color: $text-dark;

    // empty-box of the checkbox facade
    span[aria-hidden="true"]:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: $check-box-size;
      height: $check-box-size;
      border: $check-box-border-width solid $check-box-default-color;
    }
  }

  & :focus + label span[aria-hidden="true"]:before {
    @include checkbox-focus-outline;
  }

  & [type="checkbox"]:checked + label {
    span[aria-hidden="true"]:before {
      background-color: $check-box-default-color;
    }

    // check mark of the checkbox facade
    span[aria-hidden="true"]:after {
      @include icon-for("checkmark");
      position: absolute;
      top: $check-box-checkmark-position-top;
      left: $check-box-checkmark-position-left;
      font-size: $check-box-checkmark-size;
      color: #fff;
    }
  }

  & [type="checkbox"]:disabled + label {
    color: $check-box-disable-text;
    cursor: not-allowed;

    span[aria-hidden="true"]:before {
      border-color: $check-box-disable-color;
    }
  }

  & [type="checkbox"]:disabled:checked + label span[aria-hidden="true"]:before {
    background-color: $check-box-disable-color;
  }
}

@mixin checkbox-toggle-switch-focus-outline {
  @include firefox-styling;
  -webkit-box-shadow: 0 0 0 2pt $check-box-focus-color;
  -moz-box-shadow: 0 0 0 2pt $check-box-focus-color;
  box-shadow: 0 0 0 2pt $check-box-focus-color;
  @include ie-styling;
  outline: 1px dotted $check-box-disable-color;
}

@mixin checkbox-toggle-switch {
  // hide the real checkbox
  position: absolute;
  opacity: 0;

  + label {
    display: inline-block;
    position: relative;
    padding: 4px 0 0 37px + 10px;

    // toggle switch off state
    span[aria-hidden="true"] {
      display: inline-block;
      position: absolute;
      top: 0;
      left: 0;
      width: 37px;
      height: 26px;
      border-radius: 19px;
      background: $checkbox-toggle-switch-color-off;
    }

    span[aria-hidden="true"]:before {
      content: '';
      position: absolute;
      top: 1px;
      left: 1px;
      width: 24px;
      height: 24px;
      border-radius: 100%;
      background: #fff;
      transition: left $checkbox-toggle-switch-transition-speed ease-out;
    }

    span[aria-hidden="true"]:after {
      @include icon-for("checkmark");
      position: absolute;
      top: 6px;
      left: 19px;
      font-size: 11px;
      color: transparent;
    }
  }

  &:focus + label span[aria-hidden="true"] {
    @include checkbox-toggle-switch-focus-outline;
  }

  // toggle switch on state
  &[type="checkbox"]:checked + label {

    span[aria-hidden="true"] {
      background: $checkbox-toggle-switch-color-on;
    }
    
    span[aria-hidden="true"]:before {
      left: 12px;
      transition: left $checkbox-toggle-switch-transition-speed ease-out;
    }

    span[aria-hidden="true"]:after {
      color: $checkbox-toggle-switch-color-on;
      transition: color $checkbox-toggle-switch-transition-speed ease-out;
    }
  }
}


// Remove "@mixin checkbox" once all teams have updated their code to use "@mixin check-box".
// This is a Non-ADA compliant checkbox, set for deprecation in future SG version.
// Keeping "@mixin checkbox" alive to prevent build error in kp-foundation _global-styles.scss where @include checkbox($checkbox-size-mobile) is used.
// The :not(.check-box) selectors are added to prevent conflicts with the ADA compliant .check-box class
$checkbox-border-width: 2px;
$checkbox-size-mobile: 20px;
$enabled-color: color("dolphin");
$disabled-color: color("graphite");
@mixin checkbox($size, $label-size: $base-font-size) {
  &[type="checkbox"]:not(:checked):not(.check-box),
  &[type="checkbox"]:checked:not(.check-box) { 
       
    position: absolute;
    opacity: 0;

    & + label {
      margin: $size/2 0 0 0;
      color: $text-dark;
      cursor: pointer;
      display: inline-block;
      font-size: $label-size;
      position: relative;
      padding-left: $size + $small-spacing;
      padding-top: $checkbox-border-width;
      min-width: $size;

      &.-screenreader {
        padding-left: 0;
      }

      &:before {
        overflow: hidden;
        content: '';
        z-index: $midground;
        position: absolute;
        top: $checkbox-border-width;
        left: $checkbox-border-width;
        width: $size;
        height: $size;
        border: $checkbox-border-width solid $enabled-color;
        border-radius: $checkbox-border-width;
        background-color: $enabled-color;

        &:hover {
          border-color: $enabled-color;
        }
      }

      &:after {
        @include icon-for("checkmark");
        z-index: $midground;
        position: absolute;
        top: (($size/3) - $checkbox-border-width)/1.75;
        left: (($size/2) + $checkbox-border-width)/2;
        font-size: $size * 0.65;
        background-color: $enabled-color;
        color: white;
        @include media($mobile){
          top: (($size/2.5) - $checkbox-border-width)/2;
        }
      }
    }

    &:focus {
      & + label:before {
        @include checkbox-focus-outline;
      }
    }
  }

  &[type="checkbox"]:not(:checked):not(.check-box) + label:before {
    background-color: white;
  }

  &[type="checkbox"]:disabled:checked:not(.check-box) + label:before {
    border: $checkbox-border-width solid $button-disable-color;
    background-color: $button-disable-color;
  }

  &[type="checkbox"]:disabled:not(:checked):not(.check-box) + label:before,
  &[type="checkbox"]:disabled:checked:not(.check-box) + label:before {
    border: $checkbox-border-width solid $button-disable-color;
  }

  &[type="checkbox"]:disabled:checked:not(.check-box) + label:after {
    background-color: $button-disable-color;
    color: white;
  }

  &[type="checkbox"]:disabled:not(.check-box) + label {
    cursor: not-allowed;
  }

  &[type="checkbox"]:not(:checked):not(.check-box) + label:after {
    opacity: 0;
  }

  &[type="checkbox"]:checked:not(.check-box) + label:after {
    opacity: 1;
  }
}
