// Last updated: 2019-04-01 by Eric Yeh

$base-focus-color: color(inky-blue);

// vendor prefixes
@mixin cross-browser($name, $value) {
  @each $vendor in ('-webkit-', '-moz-', '-ms-', '-o-', '') {
    #{$vendor}#{$name}: #{$value};
  }
}

@mixin display-flex {
  display: -webkit-box; /* OLD - iOS */
  display: -moz-box; /* OLD - Firefox */
  display: -ms-flexbox;
  display: -webkit-flex; /* Chrome */
  display: flex;
}

// Flex-properties (for flex table, flex direction, flex grid)
@mixin flex-property_crossbrowser($property, $value) {
  @each $vendor in ('-webkit-', '-ms-', '') {
    #{$vendor}flex-#{$property}: $value;
  }
}

@mixin flex-prop($property, $value) {
  @if $property == false {
    @each $vendor in ('-webkit-', '-ms-', '') {
      #{$vendor}flex: $value;
    }
  }
  @else {
    @each $vendor in ('-webkit-', '-ms-', '') {
      #{$vendor}flex-#{$property}: $value;
    }
  }
}

@mixin flex($values) {
  -webkit-box-flex: $values;
  -moz-box-flex: $values;
  -webkit-flex: $values;
  -ms-flex: $values;
  flex: $values;
}
@mixin align-center {
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  @include flex-property_crossbrowser(align, center);
  align-items: center;
  text-align: center;
}

// focus specific for firefox
@mixin firefox-tab-focus {
  @-moz-document url-prefix() {
    outline: none;
    border: 1px dotted $base-focus-color;
    outline-offset: 0px;
  }
}

// buttons focus specific for firefox
@mixin firefox-tab-focus-buttons {
  @-moz-document url-prefix() {
    outline-offset: 2px;
    outline: 1px dotted $base-focus-color;
  }
}

// cross-browser input placeholder
@mixin placeholder {
  @at-root #{&}::-moz-placeholder { @content }
  @at-root #{&}::-webkit-input-placeholder { @content }
  @at-root #{&}:-ms-input-placeholder { @content }
}

// firefox specific styling
@mixin firefox-styling {
  @-moz-document url-prefix() {
    @content;
    box-shadow: 0 0 0 1.5pt $focus-color;
  }
}

// ie specific styling
@mixin ie-styling {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content;
    box-shadow: 0 0 0 1.5pt $focus-color;
  }
}

@mixin ms-clear { // remove Microsoft IE11 & Edge browser default clear 'X'
  input::-ms-clear {
    display: none;
  }
}
