// Last updated: 2019-04-01 by Eric Yeh

$input-border-width: 1px;
$input-active-border-color: color("interactive-blue");
$label-color: color("inky-blue");
$input-grey-color: color("graphite-dark");
$placeholder-text-color: color("dolphin");
$input-text-color: color("dolphin");
$label-line-height: 1.5em;
$label-font-style: uppercase;
$label-height: uppercase;
$error-color: color("deep-red");
$disabled-color: #FAFAFA;
$input_height--large: 60px;
$input_height--small: 50px;
$text-area-border-color: color("graphite-dark");
$clear-icon-path: 'assets/images/clear_x.svg';
$clear-icon-white-path: 'assets/images/clear_x_white.svg';
$inline-error-icon-path: 'assets/images/error-inline-16x16.svg';

@include ms-clear; // remove Microsoft IE11 & Edge browser default clear 'X' site-wide

@mixin form-messages {
  @include styling(8);
  & p {
    margin: $extra-tiny-spacing auto;
  }
}

.character-count {
  @include form-messages;
}

@mixin input_close-icon {
  display: none;
  position: absolute;
  top: 5px;
  right: 25px;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  &:before {
    content: " ";
    position: absolute;
    top: 16px;
    width: 18px;
    height: 18px;
    background: url($clear-icon-path) no-repeat;
    background-size: contain;
    background-repeat: no-repeat;
    @include media($mobile) {
      top: 11px;
    }
  }
}

@mixin text-area_global {
  @include styling(6);
  display: block;
  padding: $tiny-spacing;
  width: calc(100% - 28px);
  height: 252px;
  border: 1px solid $text-area-border-color;
  border-radius: 0;
  color: $label-color;
  -webkit-appearance: none;
  &.focus {
    border-color: $link-color;
  }
  &[data-show-lines='1'] {
    height: 54px;
  }
  &[data-show-lines='2'] {
    height: 76px;
  }
  &[data-show-lines='3'] {
    height: 98px;
  }
  &[data-show-lines='4'] {
    height: 120px;
  }
  &[data-show-lines='5'] {
    height: 142px;
  }
  &[data-show-lines='6'] {
    height: 164px;
  }
  &[data-show-lines='7'] {
    height: 186px;
  }
  &[data-show-lines='8'] {
    height: 208px;
  }
  &[data-show-lines='9'] {
    height: 230px;
  }
  &[data-show-lines='10'] {
    height: 252px;
  }
  @include media($mobile){
    @include styling(7);
    height: 234px;
    &[data-show-lines='1'] {
      height: 54px;
    }
    &[data-show-lines='2'] {
      height: 74px;
    }
    &[data-show-lines='3'] {
      height: 94px;
    }
    &[data-show-lines='4'] {
      height: 114px;
    }
    &[data-show-lines='5'] {
      height: 134px;
    }
    &[data-show-lines='6'] {
      height: 154px;
    }
    &[data-show-lines='7'] {
      height: 174px;
    }
    &[data-show-lines='8'] {
      height: 194px;
    }
    &[data-show-lines='9'] {
      height: 214px;
    }
    &[data-show-lines='10'] {
      height: 234px;
    }
  }
}

.text-area_global {
  @include text-area_global;
}

%form-control {
  @include checkbox($checkbox-size-mobile); // remove this line when all teams have updated their code to use the ADA compliant checkbox @mixin check-box
  padding: 20px;
  //height: $input_height--large;
  border: solid $input-border-width $input-grey-color;
  vertical-align: middle;
  color: $text-dark;
  &:focus {
    border-color: $input-active-border-color;
  }
  &::-webkit-input-placeholder {
    color: $placeholder-text-color;
  }
  &:-moz-placeholder {
    color: $placeholder-text-color;
  }
}

.form-control {
  @extend %form-control;
}

%form-group {
  input {
    @extend %form-control;
    @include media($desktop) {
      max-height: $input_height--large;
    }
    @include media($mobile) {
      max-height: $input_height--small;
    }
  }

  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="url"],
  textarea {
    -webkit-appearance: none; 
    border-radius: 0;
  }

  label {
    @include styling(8);
    color: $label-color;
    display: block;
    margin-bottom: $extra-tiny-spacing;
    text-transform: $label-font-style;
  }
  .form-textbox {
    input {
      padding-right: 32px;
      width: 236px;
    }
    position: relative;
    .close-icon {
      @include input_close-icon;
    }
  }
}

.form-group {
  @extend %form-group;
}

%fieldset{
  margin: 0;
  padding: 0;
  border: none;
  legend{
    @include styling(6);
    font-family: $medium-font-family;
    color: $text-dark;
    padding-bottom: $extra-tiny-spacing;
  }
}

.fieldset {
  @extend %fieldset;
}

%disabled-field {
  border: solid 1px $disabled-color;
  background-color: $disabled-color;
  cursor: not-allowed !important;
}

.disabled-field {
  @extend %disabled-field;
}

// superseded by .error-description, retain for backward compatibility
.expDesc {
  @include form-messages;
  color: $error-color;
  display: block;
  width: 100%;
}

// Error Message [start]
// move these to it's own scss file in Style Guide and place it after all form scss files
.error {
  color: $error-color;
  .form-textbox {
    input {
      border: 1px solid $error-color;
      background-color: rgba(182, 49, 37, 0.05);
    }
  }
}

.error-field,
input.error-field,
textarea.error-field,
select.error-field {
  border: 1px solid $error-color;
  background-color: rgba(182, 49, 37, 0.05);

  &.-readonly {
    padding: 0 0 12px 0; // 20px between input value to .error-description ul
    border: 0;
    color: $error-color;
    background-color: transparent;
    font-family: $medium-font-family;
  }

  &.select-dropdown {
    background-color: rgba(182, 49, 37, 0.05);
  }
}

.error-description ul {
  @include list-unstyled;
  margin: $extra-tiny-spacing - 2px 0 $extra-tiny-spacing;
  color: $error-color;

  .inline-error-icon {
    display: inline-block;
    margin-right: 10px;
    width: 16px;
    height: 16px + 2px; // align the error icon with label
    color: transparent; // for high contrast mode
    background-color: transparent; // for high contrast mode
    background-image: url($inline-error-icon-path);
    background-repeat: no-repeat;
    background-position: 0 100%; // align the error icon with label
  }
}

.check-box + label + .error-description ul,
.radio-button + label + .error-description ul,
.fieldset + .error-description ul {
  margin-top: 2.5px // 10px - half of check-box or radio-button bottom spacing
}

.error ul, // superseded by .error-description, retain for backward compatibility
ul.error // superseded by .error-description, retain for backward compatibility
{
  margin: $extra-tiny-spacing 0;
  padding-left: $small-spacing;
}
// Error Message [end]