$colors: (
  'interactive-blue': #0078b3,
  'contrast-blue': #006ba6,
  'interactive-blue-dark': #003b71,
  'kp-blue': #0078b3,
  'inky-blue': #0d1c3d,
  'dolphin': #677083,
  'graphite-dark': #d3d6dc,
  'graphite': #e9ebee,
  'graphite-light': #f4f5f6,
  'pebble': #f7f7f7,
  'midnight': #003b71,
  'sky': #90cef1,
  'moss': #4a7628,
  'grass': #57a635,
  'leaf': #a4d751,
  'burnt-sienna': #d1480c,
  'tiger-lily': #f36c0d,
  'tangerine': #fec941,
  'ocean': #066564,
  'aqua': #20a29e,
  'robin-egg': #7bd3cf,
  'acai': #bb1654,
  'fuchsia': #f05674,
  'rose': #ffa4b4,
  'plum': #583985,
  'lilac': #846ead,
  'lavender': #b2a8d2,
  'deep-red': #b63125,
  'black': #000,
  'deep-chalk': #585858,
  'mid-chalk': #666,
  'light-chalk': #d8d1ca,
  'mid-aqua': #ceecfd,
  'light-aqua': #e9f7fe,
  'pale-blue': #f0f7fa,
  'motion-blue': #94dfff,
  'white': #ffffff,
);

@function color($key) {
  @return map-get($colors, $key);
}

@function set-text-color($bgcolor) {
  @if (lightness($bgcolor) > 50) {
    @return #000000;
  } @else {
    @return #ffffff;
  }
}

