// mixins
@import "buttons-lib";

// classes
.button {
  & {
    @extend %button;
  }
  i {
    color: inherit;
    font-size: 20px;
    padding-right: $small-spacing;
    vertical-align: middle;
    line-height: 20px;
    height: 20px;
    display: inline-block;
    &.-right-icon {
      padding-right: 0;
      padding-left: $small-spacing;
    }
  }
}

// This is moved to modern because global main/modern-lib needs
//  to not have any icon related dependencies.
%-close-icon {
  &:before {
    @include icon-for("close");
  }
}

.-close {
  @extend %-close;
  @extend %-close-icon;

  @include media($device) {
    font-size: $small-font-size;
  }
  &--large {
    @extend %-close;
    @extend %-close-icon;
    font-size: $large-font-size;
  }
  &--small {
    @extend %-close;
    @extend %-close-icon;
    font-size: $small-font-size;
  }
}

.-primary-action {
  @include media($device) {
    width: 100%;
    display: block;
    white-space: normal;
    word-wrap: break-word;
    line-height: 22px;
  }
}



