$clear-icon-path: 'assets/images/clear_x.svg';
$clear-icon-white-path: 'assets/images/clear_x_white.svg';

@mixin form-messages {
  @include styling(8);
  & p {
    margin: $extra-tiny-spacing auto;
  }
}

@mixin input_close-icon {
  display: none;
  position: absolute;
  top: 5px;
  right: 25px;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  &:before {
    content: " ";
    position: absolute;
    top: 16px;
    width: 18px;
    height: 18px;
    background: url($clear-icon-path) no-repeat;
    background-size: contain;
    background-repeat: no-repeat;
    @include media($mobile) {
      top: 11px;
    }
  }
}

@mixin text-area_global {
  @include styling(6);
  display: block;
  padding: $tiny-spacing;
  width: calc(100% - 28px);
  height: 252px;
  border: 1px solid $text-area-border-color;
  border-radius: 0;
  color: $label-color;
  -webkit-appearance: none;
  & .focus {
    border-color: $link-color;
  }
  &[data-show-lines='1'] {
    height: 54px;
  }
  &[data-show-lines='2'] {
    height: 76px;
  }
  &[data-show-lines='3'] {
    height: 98px;
  }
  &[data-show-lines='4'] {
    height: 120px;
  }
  &[data-show-lines='5'] {
    height: 142px;
  }
  &[data-show-lines='6'] {
    height: 164px;
  }
  &[data-show-lines='7'] {
    height: 186px;
  }
  &[data-show-lines='8'] {
    height: 208px;
  } 
  &[data-show-lines='9'] {
    height: 230px;
  }
  &[data-show-lines='10'] {
    height: 252px;
  }
  @include media($mobile){
    @include styling(7);
    height: 234px;
    &[data-show-lines='1'] {
      height: 54px;
    }
    &[data-show-lines='2'] {
      height: 74px;
    }
    &[data-show-lines='3'] {
      height: 94px;
    }
    &[data-show-lines='4'] {
      height: 114px;
    }
    &[data-show-lines='5'] {
      height: 134px;
    }
    &[data-show-lines='6'] {
      height: 154px;
    }
    &[data-show-lines='7'] {
      height: 174px;
    }
    & [data-show-lines='8'] {
      height: 194px;
    }
    & [data-show-lines='9'] {
      height: 214px;
    }
    & [data-show-lines='10'] {
      height: 234px;
    }
  }
}

// Why is there both @extend and @mixin for the same form-control? Need to investigate.
@mixin form-control {
  padding: 20px;
  border: solid $input-border-width $input-grey-color;
  vertical-align: middle;
  color: $text-dark;
  &:focus {
    border-color: $input-active-border-color;
  }
  &::-webkit-input-placeholder {
    color: $placeholder-text-color;
  }
  &:-moz-placeholder {
    color: $placeholder-text-color;
  }
}

// Why is there both @extend and @mixin for the same form-control? Need to investigate.
%form-control {
  padding: 20px;
  border: solid $input-border-width $input-grey-color;
  vertical-align: middle;
  color: $text-dark;
  &:focus {
    border-color: $input-active-border-color;
  }
  @at-root #{&}::-webkit-input-placeholder {
    color: $placeholder-text-color;
  }
  @at-root #{&}:-moz-placeholder {
    color: $placeholder-text-color;
  }
}

// Why is @mixin form-group defined twice but different? Need to investigate.
@mixin form-group {
  input {
    @include form-control;
    @include media($desktop) {
      min-height: $xl-spacing;
      max-height: $xl-spacing;
    }
    &::-ms-clear { // remove Microsoft IE11 & Edge browser default clear 'X'
      display: none;
    }
  }
  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="url"],
  textarea {
    -webkit-appearance: none; 
    border-radius: 0;
  }
  label {
    @include styling(8);
    color: $label-color;
    display: block;
    margin-bottom: $tiny-spacing;
    text-transform: $label-font-style;
  }
}

// Why is @mixin form-group defined twice but different? Need to investigate.
@mixin form-group {
  input {
    @extend %form-control;
    max-height: $input_height--small;
    @include media($desktop) {
      max-height: $input_height--large;
    }
    &::-ms-clear { // remove Microsoft IE11 & Edge browser default clear 'X'
      display: none;
    }
  }
  input[type="text"],
  input[type="email"],
  input[type="number"],
  input[type="password"],
  input[type="search"],
  input[type="tel"],
  input[type="url"],
  textarea {
    -webkit-appearance: none; 
    border-radius: 0;
  }
  label {
    font-size: $extra-tiny-font-size;
    @include media($desktop) {
      font-size: $tiny-font-size;
    }
    color: $label-color;
    display: block;
    line-height: $label-line-height;
    margin-bottom: $tiny-spacing;
    text-transform: $label-font-style;
  }
  .form-textbox {
    input {
      padding-right: 32px;
      width: 236px;
      &::-ms-clear { // remove Microsoft IE11 & Edge browser default clear 'X'
        display: none;
      }
    }
    position: relative;
    .close-icon {
      @include input_close-icon;
    }
  }
}

@mixin disabled-field {
  border: solid 1px $disabled-color;
  background-color: $disabled-color;
  cursor: not-allowed !important;
}

@mixin error {
  color: $error-color;
  input {
    background-color: rgba(182, 49, 37, 0.05);
    border: 1px solid $error-color;
  }
}