$font-path: 'assets/fonts/';
@at-root {
  @font-face {
    font-family: "kp-icons";
    src: url("#{$font-path}kp-icons.eot");
    src: url("#{$font-path}kp-icons.eot?#iefix") format("embedded-opentype"),
    url("#{$font-path}kp-icons.woff") format("woff"),
    url("#{$font-path}kp-icons.ttf") format("truetype"),
    url("#{$font-path}kp-icons.svg#kp-icons") format("svg");
    font-weight: normal;
    font-style: normal;
  }
}


[class^="icon-"]:before,
[class*=" icon-"]:before {
  font-family: "kp-icons";
  font-weight: normal;
  font-style: normal;
  speak: none;
  text-decoration: inherit;
  text-transform: none;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

