// Last updated by Dave Hulse, 7/16/2019

@mixin -block {
  display: block;
  width: 100%;
}

@mixin rounded-input {
  @include cross-browser("appearance", "none");
  display: inline-block;
  padding: 13px $button-padding;
  min-width: $button-min-width;
  font-weight: normal;
  cursor: pointer;
  border: none;
  user-select: none;
  text-align: center;
  height: auto;
  line-height: 20px;
  vertical-align: middle;
  white-space: normal;
  word-wrap: break-word;
  -webkit-font-smoothing: antialiased;
  @include media($tablet-up) {
    line-height: 22px;
    padding-top: 17px;
    padding-bottom: 17px;
  }
}

@mixin button-($position) {
  text-align: $position;
}

@mixin button_position($position) {
  float: $position;
}

@mixin button {
  @include rounded-input;
  @include cross-browser("transition", "background-color 0.2s ease,\a   color 0.2s ease,\a   border-color 0.2s ease");
  background-color: $button-color;
  color: white;
  border: $button-border-size transparent solid;
  &:hover,
  &:focus,
  &:active {
    background-color: $button-hover-color;
    color: white;
  }
  &.-action {
    background-color: transparent;
    border-color: white;
    &:hover,
    &:focus {
      background-color: white;
      color: $button-hover-color;
    }
    &.-inverted {
      border-color: white;
      color: $button-color;
      &:hover,
      &:focus {
        background-color: white;
        color: $button-hover-color;
      }
    }
  }
  &.-inverted {
    background-color: #ffffff;
    color: $button-color;
    border-color: $button-color;
    &:hover,
    &:focus,
    &:active {
      background-color: white;
      color: $button-hover-color;
      border-color: $button-hover-color;
    }
    &:disabled,
    .-disabled,
    &.-disabled {
      background-color: $button-inverted-disable-color;
      border-color: $button-inverted-disable-color;
    }
    &--white {
      color: $button-color--white;
      border: $button-border-size solid $button-color--white;
      background-color: transparent;
      &:hover,
      &:focus,
      &:active {
        background-color: $button-color--white;
        color: $button-color;
      }
    }
  }
  &.-small {
    min-width: $button-small-min-width;
    font-size: $tiny-font-size;
  }
  &:disabled,
  .-disabled {
    background-color: $button-disable-color;
    &:hover {
      cursor: not-allowed;
      border-color: $button-disable-color;
    }
    &.-inverted {
      color: $button-disable-color;
      border: $button-border-size solid $button-disable-color;
    }
  }
  &.-left {
    @include button_position(left);
  }
  &.-center {
    @include button_position(center);
  }
  &.-right {
    @include button_position(right);
  }
  &.-tertiary {
    background: none;
    min-width: auto;
    border: none;
    color: $link-color;
    padding: 0;
    height: auto;
    margin: 14px  0;
    @include media($tablet-up){
      margin: 19px 0;
    }
    &:hover,
    &:focus,
    &:active {
      color: $link-hover-color;
    }
  }
  @include media($mobile) {
    width: 100%;
    & + & {
      margin-top: $tiny-spacing;
    }
  }
}

@mixin button-unstyled {
  @include cross-browser("appearance", "none");
  color: $button-color;
  border: 0 solid transparent;
  background-color: transparent;
  text-align: left;
  padding: 0;
}

@mixin -close-styles {
  color: $button-color;
  font-size: $small-font-size;
  border: none;
  background: none;
}

@mixin -close {
  @include -close-styles;
  @include media($device) {
    font-size: $small-font-size;
  }
  &--large {
    @include -close-styles;
    font-size: $large-font-size;
  }
  &--small {
    @include -close-styles;
    font-size: $small-font-size;
  }
}

