$color-array: (
	inky-blue: #0d1c3d,
	dolphin: #677083,
	white: #fff,
);
$weight-array: (
	book: 'Gotham 4r',
	light: 'Gotham 3r',
	medium: 'Gotham 5r',
	bold: 'Gotham 7r',
);
// Functions
@each $color-name, $color-value in $color-array {
	// single-dash is preferred, but will support double-dash for legacy implementations
	%-#{$color-name},
	%--#{$color-name} {
		// escapes double hyphen for IE and legacy mobile Safari suport
		color: $color-value;
	}
}

@mixin caps {
	text-transform: uppercase;
}
@mixin muted {
	color: darken(color(graphite-dark), 20%);
}

@each $weight-name, $weight-value in $weight-array {
	// single-dash is preferred, but will support double-dash for legacy implementations
	%-#{$weight-name},
	%--#{$weight-name} {
		// escapes double hyphen for IE and legacy mobile Safari suport
		font-family: $weight-value;
	}
}

@mixin inverted-link {
	&,
	&:visited {
		color: $text-light;
	}
	&:hover,
	&:focus {
		color: color(interactive-blue);
	}
}

@mixin styling($i) {
	@if $i==1 {
		font-size: $xxl-font-size;
		line-height: 64px;
		letter-spacing: -1px;
		font-weight: normal;
		@include media($mobile) {
			body:not(.-locked) &:not(.-locked) {
				font-size: $xl-font-size;
				line-height: 40px;
			}
		}
		&-marketing {
			font-size: $xxl-font-size;
			line-height: 74px;
			@include media($mobile) {
				body:not(.-locked) &:not(.-locked) {
					font-size: $xl-font-size;
					line-height: 50px;
				}
			}
		}
	}
	@if $i==2 {
		font-size: $xl-font-size;
		line-height: 40px;
		font-weight: normal;
		@include media($mobile) {
			body:not(.-locked) &:not(.-locked) {
				font-size: $large-font-size;
				line-height: 32px;
			}
		}
		&-marketing {
			font-size: $xl-font-size;
			line-height: 50px;
			@include media($mobile) {
				body:not(.-locked) &:not(.-locked) {
					font-size: $large-font-size;
					line-height: 42px;
				}
			}
		}
	}
	@if $i==3 {
		font-size: $large-font-size;
		font-weight: normal;
		line-height: 32px;
		@include media($mobile) {
			body:not(.-locked) &:not(.-locked) {
				font-size: $medium-font-size;
				line-height: 24px;
			}
		}
		&-marketing {
			font-size: $large-font-size;
			font-weight: normal;
			line-height: 42px;
			@include media($mobile) {
				body:not(.-locked) &:not(.-locked) {
					line-height: 30px;
				}
			}
		}
	}
	@if $i==4 {
		font-size: $medium-font-size;
		line-height: 24px;
		font-weight: normal;
		@include media($mobile) {
			body:not(.-locked) &:not(.-locked) {
				font-size: $small-font-size;
				line-height: 22px;
			}
		}
		&-marketing {
			font-size: $medium-font-size;
			line-height: 30px;
			@include media($mobile) {
				body:not(.-locked) &:not(.-locked) {
					font-size: $small-font-size;
					line-height: 28px;
				}
			}
		}
	}
	@if $i==5 {
		font-size: $small-font-size;
		line-height: 22px;
		font-weight: normal;
		&-marketing {
			font-size: $small-font-size;
			line-height: 28px;
		}
	}
	@if $i==6 {
		font-size: $base-font-size;
		line-height: 22px;
		font-weight: normal;
		@include media($mobile) {
			body:not(.-locked) &:not(.-locked) {
				font-size: $base-mobile-font-size;
				line-height: 20px;
			}
		}
		&-marketing {
			font-size: $base-font-size;
			line-height: 26px;
			@include media($mobile) {
				body:not(.-locked) &:not(.-locked) {
					font-size: $base-mobile-font-size;
					line-height: 24px;
				}
			}
		}
	}
	@if $i==7 {
		font-size: $base-font-size;
		line-height: 22px;
		font-weight: normal;
		@include media($mobile) {
			font-size: $base-mobile-font-size;
			line-height: 20px;
		}
		&-marketing {
			font-size: $base-font-size;
			line-height: 24px;
			@include media($mobile) {
				font-size: $base-mobile-font-size;
				line-height: 24px;
			}
		}
	}
	@if $i==8 {
		font-size: $tiny-font-size;
		line-height: 18px;
		font-weight: normal;
		&-marketing {
			font-size: $tiny-font-size;
			line-height: 22px;
		}
		// single-dash is preferred, but will support double-dash for legacy implementations
		&%-caps,
		&%--caps {
			// escapes double hyphen for IE and legacy mobile Safari suport
			@include caps;
		}
	}
	@if $i==9 {
		font-size: $extra-tiny-font-size;
		line-height: 16px;
		font-weight: normal;
		&-marketing {
			font-size: $extra-tiny-font-size;
			line-height: 18px;
		}
		// single-dash is preferred, but will support double-dash for legacy implementations
		&%-caps,
		&%--caps {
			// escapes double hyphen for IE and legacy mobile Safari suport
			@include caps;
		}
	}
}

//Marketing Mixin
@mixin styling-marketing($i) {
	@include styling($i);
	@if $i==1 {
		line-height: 74px;
		@include media($mobile) {
			line-height: 50px;
		}
	}
	@if $i==2 {
		line-height: 50px;
		@include media($mobile) {
			line-height: 42px;
		}
	}
	@if $i==3 {
		line-height: 42px;
		@include media($mobile) {
			line-height: 30px;
		}
	}
	@if $i==4 {
		line-height: 30px;
		@include media($mobile) {
			line-height: 28px;
		}
	}
	@if $i==5 {
		line-height: 28px;
	}
	@if $i==6 {
		line-height: 26px;
		@include media($mobile) {
			line-height: 24px;
		}
	}
	@if $i==7 {
		line-height: 24px;
		@include media($mobile) {
			line-height: 24px;
		}
	}
	@if $i==8 {
		line-height: 22px;
		& %\--caps {
			// escapes double hyphen for IE and legacy mobile Safari suport
			@include caps;
		}
	}
	@if $i==9 {
		line-height: 18px;
		& %\--caps {
			// escapes double hyphen for IE and legacy mobile Safari suport
			@include caps;
		}
	}
}
