@mixin content-container {
  &::before {
    clear: both;
    content: "";
    display: table;
    height: 0;
  }
}

@mixin center-content {
  @include media($desktop-large) {
    margin-left: auto;
    margin-right: auto;
    max-width: $max-width;
  }

  @include media($desktop) {
    padding: $medium-spacing;
  }
}

@mixin hide-fade-in {
  opacity: 0;
  visibility: hidden;
  width: 0;
  height: 0;
  float: left;
}

@mixin show-fade-in {
  visibility: visible;
  opacity: 1;
  width: auto;
  height: auto;
  float: none;
}

$base-focus-color: color(inky-blue);
// vendor prefixes
@mixin cross-browser($name, $value) {
  @each $vendor in ('-webkit-', '-moz-', '-ms-', '-o-', '') {
    #{$vendor}#{$name}: #{$value};
  }
}
// Flex-properties (for flex table, flex direction, flex grid)
@mixin flex-property_crossbrowser($property, $value) {
  @each $vendor in ('-webkit-box-', '-moz-box-', '-ms-', '-o-', '') {
    #{$vendor}flex-#{$property}: #{$value};
  }
}


// focus specific for firefox
@mixin firefox-tab-focus {
  @-moz-document url-prefix() {
    outline: none;
    border: 1px dotted $base-focus-color;
    outline-offset: 0px;
  }
}

// buttons focus specific for firefox
@mixin firefox-tab-focus-buttons {
  @-moz-document url-prefix() {
    outline-offset: 2px;
    outline: 1px dotted $base-focus-color;
  }
}

// cross-browser input placeholder
@mixin placeholder {
  @at-root #{&}::-moz-placeholder { @content }
  @at-root #{&}::-webkit-input-placeholder { @content }
  @at-root #{&}:-ms-input-placeholder { @content }
}

// firefox specific styling
@mixin firefox-styling {
  @-moz-document url-prefix() {
    @content;
    box-shadow: 0 0 0 1.5pt $focus-color;
  }
}

// ie specific styling
@mixin ie-styling {
  @media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
    @content;
    box-shadow: 0 0 0 1.5pt $focus-color;
  }
}
//Clearfix
@mixin clearfix() {
  &:before,
  &:after {
    content: " "; // 1
    display: table; // 2
  }
  &:after {
    clear: both;
  }
}
